<script>
  import * as web from '../js/source.js'
  export default {
    data() {
      return {
        show: true,
        flag: false,
        webDataList: web.webSource,
        theadList: web.theadList,
      }
    },
    created() {
      this.webDataList.map((data) => {
        return Object.assign(data, { show: true })
      })
    },
    methods: {
      showTable(index) {
        const tempShow = !this.webDataList[index].show
        this.webDataList[index] = Object.assign(this.webDataList[index], {
          show: tempShow,
        })
      },
    },
  }
</script>
